/*
*
* ========================================================
* HERO SECTION
* ========================================================
*
*/
.hero-home {

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        display: block;
        background: linear-gradient(to top, $brand-primary, rgba($brand-primary, 0.25));
        z-index: 1;
    }
}



/*
*
* ========================================================
* INFO SECTION
* ========================================================
*
*/
.info .container {
    transform: translateY(-5rem);
    @include media-breakpoint-up(lg) {
        transform: translateY(-50%);
    }
    z-index: 9999;
    position: relative;
}



/*
*
* ========================================================
* SERVICE SECTION
* ========================================================
*
*/
.services-item {
    &-text {
        color: $text-muted;
        transition: all 0.5s;
    }

    &:hover {
        background: $brand-primary;
        color: #fff;

        .services-item-text {
            color: #fff;
        }

        .services-icon {
            background: #fff;
            color: $brand-primary;

            &::before {
                border-color: #fff;
            }
        }
    }
}

.services-item {
    transition: all 0.5s;
}

.services-icon {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background: $brand-primary;
    color: #fff;
    display: inline-block;
    text-align: center;
    line-height: 90px;
    position: relative;
    transition: all 0.5s;

    &::before {
        content: '';
        width: 96px;
        height: 96px;
        border-radius: 50%;
        position: absolute;
        top: -8px;
        left: -8px;
        display: block;
        border: 2px solid rgba($brand-primary, 0.5);
        transition: all 0.5s;
    }
}



/*
*
* ========================================================
* DOCTORS SECTION
* ========================================================
*
*/
.doctor {
    &:hover {
        .doctor-overlay {
            opacity: 1;
        }
    }
}
.doctor-overlay {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba($brand-primary, 0.65);
    transition: all 0.5s;
    opacity: 0;
}

.doctor-social-link {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    color: $brand-primary;
    background: rgba(255, 255, 255, 0.88);
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid $brand-primary;
    transition: all 0.3s;

    &:hover, &:focus {
        text-decoration: none;
    }
}




.contact-icon {
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #fff;
    font-size: 1.4rem;
}
