/*
*
* ========================================================
* GENERAL
* ========================================================
*
*/

$footer-bg: #1d2024;

body {
    perspective: 500px;
}

section, .section {
    padding: 6.5rem 0;
    position: relative;
}



/*
*
* ========================================================
* NAVBAR
* ========================================================
*
*/
.navbar {
    z-index: 999;
    position: relative;

    .nav-link {
        @include media-breakpoint-down(md) {
            padding-top: 0.5rem !important;
            padding-bottom: 0.5rem !important;
            border-left: none !important;
        }
    }

    #searchToggler {
        border-left: 1px solid #dee2e6;
    }
}


#search {
    position: relative;
    z-index: 80;
    transition: all 0.3s;
    margin-top: -100px;
    opacity: 0;

    &.active {
        margin-top: 0;
        opacity: 1;
    }
}


/*
*
* ========================================================
* FOOTER
* ========================================================
*
*/
.footer {
    background: $footer-bg;
    padding: 6.5rem 0 0;
}

.footer-link {
    color: #ccc;
    position: relative;
    padding-left: 1.5rem;
    transition: all 0.3s;
    font-size: 0.9rem;
    font-weight: 300;

    &::before {
        content: '\f22d';
        font-family: 'Font Awesome\ 5 Free';
        font-weight: 900;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        display: block;
        color: #ccc;
        transition: all 0.3s;
    }

    &:hover, &:focus {
        color: $brand-primary;
        text-decoration: none;

        &::before {
            color: $brand-primary;
        }
    }
}

.copyrights {
    margin-top: 6.5rem;
    background: darken($footer-bg, 3%);
}
